"use client";

import React, { useState, useEffect } from "react";
import { Settings, LogOut } from "lucide-react";
import { useRouter } from "next/navigation";
import Date from '../date/date';

export default function Header() {
  const [currentTime, setCurrentTime] = useState("");
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const router = useRouter();


  const handleLogout = () => {
    localStorage.removeItem("token");
    router.push("/auth/login");
  };

  return (
    <header className="mx-4 my-2 rounded-lg bg-white shadow-sm relative">
      <div className="h-20 px-4 flex items-center justify-between">
        {/* Left side - Dashboard text */}
        <div className="relative">
          <a href="#" className="text-[#5D56BD] text-medium">
            My Stuff
          </a>
        </div>

        {/* Right side with search, datetime, and profile */}
        <Date/>
      </div>

      {/* Horizontal menu with increased padding and top margin */}
      <div className="w-full px-4 py-2">
        <div className="flex space-x-6 text-sm text-[#5D56BD]">
          <a href="#" className="hover:underline">All Assignments</a>
          <a href="#" className="hover:underline">Bookmarks</a>
          <a href="#" className="hover:underline">Schedules</a>
          <a href="#" className="hover:underline">My Drafts</a>
        </div>
      </div>
    </header>
  );
}